.oauth-providers>ul{width:180px;margin-left:auto;margin-right:auto}.oauth-providers>ul>li{position:relative;margin-bottom:30px}.oauth-providers>ul>li>a{display:block;width:180px;line-height:22px;padding:8px 12px;border:1px solid rgba(0,0,0,.15);border-radius:2px;box-sizing:border-box;background-color:#236a97;color:#fff;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.oauth-providers>ul>li>a:focus,.oauth-providers>ul>li>a:hover{box-shadow:0 0 16px rgba(0,0,0,.2)}.oauth-providers>ul>li>a>span{padding-left:6px}.oauth-providers-help{position:absolute;top:12px;right:-32px}.oauth-providers+.login-form{padding-top:30px;border-top:1px solid #e6e6e6}